/* Base typo */
body {
  font-family: 'Lato', sans-serif!important;
  font-size: 17px;
}
h1 {
    font-size: 40px;
    font-weight: 900;
}
h2 {
    font-weight: 700;
    font-size: 35px;
}
.tm-dark h2 {
    color: #666;
    font-size: 20px;
    font-weight: 400;
}
h3 {
    font-size: 20px;
    font-weight: 700;
}
.tm-dark h3 {
    color: #333;
}
h1 + h2 {
    font-size: 20px;
    font-weight: 400;
    color: #777;
}
a, a:link {
    color: #DD7E02;
}
h3 a {
    font-weight: bold;
}
p {
    font-size: 17px;
    color: #4A4A4A;
    line-height: 25px;
}
.tm-dark p {
    // color: rgba(255,255,255,0.6);
    color: rgba(0,0,0,0.6);
}

/* Utilities */
.uk-block {
    padding: 150px 0;
}
.uk-block-top {
    padding: 150px 0 0;
}
.tm-panel {
    padding: 30px;
}
button, .tm-button {
    background: #f9f9f9;
    color: #444;
    display: inline-block;
    padding: 5px 10px;
    border-radius: 2px;
    border: 0;
}
.tm-button:hover {
    text-decoration: none;
    color: #fff;
}
.tm-button-medium {
    padding: 10px 25px;
}
.tm-button-large {
    padding: 20px 40px;
}
.tm-button-primary {
    background: #373330;
}
.tm-button-secondary {
    background: #373330;
    color: #fff;
}

/* Header bg */
header {
    background-image: url(header-inner-white.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    min-height: 270px;
}
.tm-home header {
  height: 100vh;
  background-image: url(home-bg-white.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-color: #fff;
}

/* Branding */
.tm-branding {
  margin-top: 50px;
  display: inline-block;
  font-size: 20px;
  position: relative;
  font-weight: 700;
}
.tm-branding:hover {
    color: rgba(255,255,255,0.8);
    text-decoration: none;
}
.tm-branding:active {
    color: rgba(255,255,255,0.8);
    text-decoration: none;
    position: relative;
    bottom: -1px;
}
.tm-branding span {
  display: block;
  font-size: 35px;
  font-weight: 900;
  margin: 4px 0 0 -1px;
  text-shadow: 0px 1px 1px rgba(30,72,105,0.36);
}
.tm-branding svg {
    position: absolute;
    top: 0;
    right: 2px;
}
.tm-branding:active svg {
    top: 2px;
}

/* Nav */
nav {
  margin-top: 70px;
}

.tm-branding,
.tm-branding:link {
  color: #fff;
}
nav {
    a, a:link {
      margin-left: 25px;
      display: inline-block;
      color: rgba(255,255,255,0.7);
    }
    a:hover {
        color: inherit;
        text-decoration: none;
    }
    a.current {
        color: #fff;
    }
}

/* Hero */
.tm-hero {
  margin-top: 30%;
  text-align: center;
}
.tm-hero * {
  color: #fff;
  text-shadow: 0px 1px 1px rgba(30,72,105,0.36);
}

/* Main */
main {
    background: #fff;
    padding-bottom: 125px;
    position: relative;
}
