//     height: 94px;
//     position: absolute;
//     background: transparent url(curve-grey.png) no-repeat;
//     background-size: cover;
//     bottom: -12px;
// }
main h1 {
    color: #222;
}
main h1 i {
    display: none;
}
main h1 sup {
    background: #608F2A;
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 10px;
    padding: 2px 6px;
    display: inline-block;
    line-height: 16px;
    border-radius: 2px;
    position: relative;
    top: -20px;
}
main h1 strong {
    border-bottom: 2px solid #BE6031;
}
.tm-home main figure {
  bottom: -100px
}

/* News */
.tm-news {
    background: #F2F2F2 url(curve-grey-btm.jpg) bottom no-repeat;
    background-size: contain;
    padding-bottom: 75px;
    padding-top: 10px;
}
.tm-news .tm-panel {
    background-image: linear-gradient(-180deg, #FFFFFF 26%, rgba(255,255,255,0.00) 100%);
    border-radius: 2px;
}
.tm-news .tm-panel:hover {
    background: #fff;
}

/* Blocks */
.tm-blocks .tm-panel {
    position: relative;
    padding-left: 50px;
}
.tm-blocks .tm-panel h3 {
    margin-top: 0;
}
.tm-blocks .tm-dark .tm-panel img {
    position: absolute;
    top: 25px;
    left: 0;
}
.tm-included .tm-panel img {
    position: relative;
    top: -2px;
}

/* Block */
.tm-single-title {
    width: auto;
    display: inline-block;
}
.tm-single-title h1{
    width: auto;
    position: relative;
    top: -5px;
}
div.tm-features {
  border: solid rgba(255,255,255,0.1);
  border-width: 1px 0;
  padding: 3em 0 1em;
}
div.tm-features div {
  margin-bottom: 2em;
}
.tm-media-grid figure {
    margin-bottom: 30px;
}

/* Footer */
footer {
    padding: 50px 0;
    input {
      border: 0;
      background: #f7f7f7;
      padding: 9px 11px;
      display: inline-block;
      min-width: 200px;
    }
    button {
        padding: 9px 11px;
        background: #333;
        color: #fff;
        font-weight: 700;
    }
}
.tm-links a {
    display: block;
    line-height: 2;
}
.tm-about p:first-of-type {
    max-width: 85%;
}

